@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&family=Raleway:wght@400&display=swap');

/* Global Styles */
html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
}

p, li, span {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

section {
  padding: 100px 20px;
}

/* Default Heading Sizes */
h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 1.6rem;
}

h3 {
  font-size: 1.75rem;
}

p {
  font-size: 1.2rem;
}

button {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

/* Mobile-Specific Styling */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

/* Overlay-Specific Styling */
.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #64ffda;
  z-index: 1000;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  box-sizing: border-box;
}

.mobile-menu-overlay.active {
  display: flex;
}

/* Use a unique class for overlay headings to avoid conflicts */
.overlay-heading {
  font-family: 'Montserrat', sans-serif;
  font-size: 6vw;  /* Use vw for responsive scaling */
  line-height: 1.2;
  text-align: center;
  margin: 10px 0;
  padding: 0 15px;
  max-width: 90vw;  /* Ensure heading doesn't exceed the viewport width */
  word-wrap: break-word;
}

@media (min-width: 769px) {
  .overlay-heading {
    font-size: 2.5rem;
  }
}

/* Adjust paragraph size for the overlay */
.overlay-text {
  font-size: 1.2rem;
  text-align: center;
  padding: 0 15px;
  max-width: 90vw;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .overlay-heading {
    font-size: 5vw;  /* Slightly smaller heading on mobile */
  }

  .overlay-text {
    font-size: 1rem;
  }
}
